import React, { useState, useEffect } from "react";

import { BdsInputPassword, BdsInput } from 'blip-ds/dist/blip-ds-react';
import { showToast } from "../../api/commomService";
import configService from '../../services/getConfigVtex';
import Switch from "react-switch";

import getNameSpaceWhatsApp from '../../services/getNameSpaceWhatsApp';
import guid from '../../utils/guid';

import "./index.css"


const Config = () => {
    const [key, setKey] = useState(localStorage.getItem('plugin-vtex-key'));
    const [key2, setKey2] = useState(localStorage.getItem('plugin-vtex-key2'));
    const [key3, setKey3] = useState(localStorage.getItem('plugin-vtex-key3'));
    const [statusService, setStatusService] = useState("")

    useEffect(async () => {
        const resultNameSpace = await getNameSpaceWhatsApp(guid(), localStorage.getItem('info'));
        const resultConfig = await configService(resultNameSpace.data.resource.name)
        const resultBooleanServices =  resultConfig.data.message === 'Ativado' ? true : false
        setCheckedServices(resultBooleanServices);
    }, [])


    const [checkedServices, setCheckedServices] = useState(false);
  
    const setValueKey = (event) => {

        event.preventDefault();
        setKey(event.detail.value)

    }
    const setValueKey2 = (event) => {

        event.preventDefault();
        setKey2(event.detail.value)

    }

    const setValueKey3 = (event) => {

        event.preventDefault();
        setKey3(event.detail.value)

    }

    const setValueKeyLocalStorage = () => {

        if (!key) {
            showMessage('O campo X-VTEX-API-AppToken precisa ser preenchido!', 'danger', 'Notice!');
            return ;
        }
        if (!key2) {
            showMessage('O campo X-VTEX-API-AppKey precisa ser preenchido!', 'danger', 'Notice!');
            return ;
        }

        if (!key3) {
            showMessage('O campo URL precisa ser preenchido!', 'danger', 'Notice!');
            return;
        }

        localStorage.setItem("plugin-vtex-key", key);
        localStorage.setItem("plugin-vtex-key2", key2);
        localStorage.setItem("plugin-vtex-key3", key3);

        showMessage('Informação gravada com sucesso!', 'success', 'Notice!');
    }
    const showMessage = (msg, type) => {

        setTimeout(() => {
            showToast({ type: type, message: msg })
        }, 100);

    }

    return (
        <>
            <div className="container w-100">
                <div className="row">

                    <div className="col-9">
                        <label>X-VTEX-API-AppToken:</label>
                        <BdsInputPassword  icon="data-security" label="Digite sua chave AppToken" onBdsInputPasswordChange={(event) => setValueKey(event)} value={key} />
                        <br></br>
                        <label>X-VTEX-API-AppKey:</label>
                        <BdsInputPassword icon="data-security" label="Digite sua chave AppKey" onBdsInputPasswordChange={(event) => setValueKey2(event)} value={key2} />
                        <br></br>
                        <label>URL:</label>
                        <BdsInput icon="site" label="Digite a URL do seu e-commerce VTEX" onBdsChange={(event) => setValueKey3(event)} value={key3} />
                        <br></br>
                        <button onClick={setValueKeyLocalStorage} className="bp-btn bp-btn--bot bp-btn--large" style={{ width: '100%' }}>Salvar</button>

                    </div>

                    <div className="col-3" >
                        <label>Status do Serviço:</label><br></br>
                        <Switch 
                  checked={checkedServices} onColor="#3f7de8" />
                        {/* <div style={{ display: 'flex', justifyContent: 'start' }}>
                            {statusService === 'Ativado' ? (
                                <><span >{statusService}</span><span className="active-message-circle-green"></span></>)
                                : (<><span >{statusService}</span><span className="active-message-circle-red"></span></>)}
                        </div> */}
                    </div>
                </div>
                <br></br>
                <hr />
            </div>

        </>
    )

};

export default Config;
